<template>
    <div class="login">
        <div class="contain">
            <div class="left">
                <img :src=imgUrl alt="">
            </div>
            <div class="right">
                <div><el-input placeholder="" v-model="loginuser.username"></el-input>
                </div>
                <div>: <el-input placeholder="" v-model="loginuser.userpwd" show-password></el-input>
                </div>
                <div class="button">
                    <el-button type="primary" @click="login"></el-button>
                    <el-button type="primary" @click="regmember"></el-button>
                </div>
            </div>

            <div class="register" v-if="register">
                <Scroll class="regscroll">
                    <div><el-input placeholder="" v-model="user.username"></el-input>
                    </div>
                    <div>: <el-input placeholder="" show-password v-model="user.userpwd"></el-input>
                    </div>
                    <div>
                        <div></div>
                        <input placeholder="" type="checkbox" :checked="sex == 1" @click="sex = 1">
                        <input placeholder="" type="checkbox" :checked="sex == 2" @click="sex = 2">
                    </div>
                    <div>: <el-input placeholder="" v-model="user.age"></el-input>
                    </div>
                    <div>: <el-input placeholder="" v-model="form.email"></el-input>

                    </div>
                    <div>
                        <el-button type="primary" @click="getcode"></el-button>
                    </div>
                    <div>: <el-input placeholder="" v-model="form.code"></el-input>

                    </div>
                    <!-- <div class="button">
                        <el-button type="primary" @click="commit"></el-button>
                    </div> -->
                    <div>
                        <el-button type="primary" @click="submit"></el-button>
                        <el-button @click="cancel"></el-button>
                    </div>
                </Scroll>

            </div>


        </div>
    </div>
</template>
<script>
import { gettoken, getemail ,registermem} from '@/network/headpage.js'
import Scroll from 'components/common/scroll/Scroll'
export default {
    name: 'login',
    data() {
        return {
            sex: 1,
            loginuser:{
                username: '',
                userpwd: ''
            },
            user: {
                username: '',
                userpwd: '',
                headimg:this.baseurl+'/resource/img/.png',
                email: '',
                sex: '',
                age: ''
            },
            register: false,
            form: {
                email: '',
                code: ''
            }
        }
    },
    computed: {
        imgUrl() {
            if (this.register) {
                return this.baseurl + "/resource/img/.gif"
            } else {
                return this.baseurl + "/resource/img/%E6%99%B4%E5%A4%A9.png"
            }
        }
    },
    components: {
        Scroll
    },
    methods: {
        login() {
            let data = {
                username: this.loginuser.username,
                userpwd: this.loginuser.userpwd
            }
            gettoken(data).then((res) => {
                if (res.data.state == 200) {
                    this.$toast.show(data.username+'')
                    localStorage.setItem('token', res.data.token)
                    this.$store.commit('addtoken')
                    this.$store.commit('commituser',this.user)

                } else {
                    this.$toast.show(res.data.msg)
                }


                //this.$store.commit('addition',res.data.info)
            })
        },
        getinfo() {

        },
        regmember() {
            this.register = true
        },
        submit() {
            var result={}
            result.code=this.form.code
            result.user=this.user
            console.log(this.user)
            registermem(JSON.stringify(result)).then(res=>{
                if(res.data.state==200){
                    this.register=false
                }
                c
                this.$toast.show(res.data.msg)     
            })

        },
        cancel() {
            this.register = false
        },
        getcode() {
            
            this.user.email = this.form.email
            console.log(this.user)
            if (this.sex == 1) {
                this.user.sex = ''
            } else if (this.sex == 2) {
                this.user.sex = ''
            }
            
            getemail(JSON.stringify(this.user)).then(res => {
                console.log(res)
                
                this.$toast.show(res.data.msg)

            })
        }
    }
}
</script>
<style scoped lang="less">
//767px 
@media screen and (max-width:767px) {
    .login {
        width: 80%;
        height: 400px;
        margin: 30px auto;
        border: 2px solid black;
        border-radius: 4px;
        background-color: rgba(100, 100, 100, .4);

        .contain {
            display: flex;
            flex-direction: column;
            height: 100%;
            position: relative;

            .left {
                flex: 3;
                padding: 20px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 4px;

                }
            }

            .right {
                padding: 5px;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;

                div {
                    margin-top: 10px;
                    color: #fff;

                }

                .button {

                    display: flex;

                }
            }

            .register {
                position: absolute;
                top: 45%;

                right: 0px;
                padding: 5px;
                width: 100%;
                height: 55%;
                overflow: hidden;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;
                background-color: #fff;
                transition: all linear .5s;

                .regscroll {
                    top: 0px;
                    left: 0px;
                    bottom: 0px;
                    margin: 20px 0px;
                    padding: 5px;

                    div {
                        margin-top: 10px;
                        color: black;
                        font-family: '';

                    }

                    .button {

                        display: flex;

                    }
                }


            }
        }
    }
}

// 768px 991px 
@media screen and (min-width:768px) and (max-width:991px) {

    .login {
        width: 80%;
        height: 400px;
        margin: 30px auto;
        border: 2px solid black;
        border-radius: 4px;
        background-color: rgba(100, 100, 100, .4);

        .contain {
            display: flex;
            height: 100%;
            position: relative;

            .left {
                flex: 3;
                padding: 20px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 4px;

                }
            }

            .right {
                padding: 5px;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;

                div {
                    margin-top: 10px;
                    color: #fff;

                }

                .button {

                    display: flex;

                }
            }

            .register {
                position: absolute;
                top: 0px;
                right: 0px;
                padding: 5px;
                width: 40%;
                height: 100%;
                overflow: hidden;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;
                background-color: #fff;
                transition: all linear .5s;

                .regscroll {
                    top: 0px;
                    left: 0px;
                    bottom: 0px;
                    margin: 20px 0px;
                    padding: 5px;

                    div {
                        margin-top: 10px;
                        color: black;
                        font-family: '';

                    }

                    .button {

                        display: flex;

                    }
                }


            }
        }
    }
}

//992px 
@media screen and (min-width : 992px) {
    .login {
        width: 60%;
        height: 500px;
        margin: 30px auto;
        border: 2px solid black;
        border-radius: 4px;
        background-color: rgba(100, 100, 100, .4);

        .contain {
            display: flex;
            height: 100%;
            position: relative;

            .left {
                flex: 3;
                padding: 20px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 4px;

                }
            }

            .right {
                padding: 5px;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;

                div {
                    margin-top: 10px;
                    color: #fff;

                }

                .button {

                    display: flex;

                }
            }

            .register {
                position: absolute;
                top: 0px;
                right: 0px;
                padding: 5px;
                width: 40%;
                height: 100%;
                overflow: hidden;
                right: 2;
                flex: 2;
                display: flex;
                flex-direction: column;
                justify-content: center;
                background-color: #fff;
                transition: all linear .5s;

                .regscroll {
                    top: 0px;
                    left: 0px;
                    bottom: 0px;
                    margin: 20px 0px;
                    padding: 5px;

                    div {
                        margin-top: 10px;
                        color: black;
                        font-family: '';

                    }

                    .button {

                        display: flex;

                    }
                }


            }
        }
    }
}
</style>


