<template>
    <div class="weather">
        <div id="he-plugin-standard"></div>
    </div>
</template>
<script>
export default {
    name: 'weather',
    mounted() {

        // 
        window.WIDGET = {
            "CONFIG": {
                "layout": "1",
                "width": "450",
                "height": "150",
                "background": "1",
                "dataColor": "FFFFFF",
                "borderRadius": "5",
                "key": "8d19344c62f74abea1b0d53b0292e703"
            }
        }
        const script = document.createElement('script')
        script.type = 'text/javascript'
        script.src = "https://widget.qweather.net/standard/static/js/he-standard-common.js?v=2.0"
        document.getElementsByTagName('head')[0].appendChild(script)
    }

}
</script>
<style scoped >
 .weather {
    width:500px;
    margin:20px auto;
}
</style>


