import { request, requestemail } from '../network/request'


export function gettoken(info) {
    return request({
        url: '/members/baseinfo',
        method: 'post',
        data: info
    })

}
export function getemail(info) {
    return request({
        url: '/members/sendemail',
        method: 'post',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8',
            'Accept': 'application/json'
        },
        data: info
    })

}
export function registermem(info) {
    return request({
        url: '/members/registermem',
        method: 'post',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8',
            'Accept': 'application/json'
        },
        data: info
    })

}


